import { tasktype } from '@/utils/consts.js';

export default {
	props: {
		dataSource: {
			type: Object,
			default: () => ({})
		}
	},
	data() {
		return {
			tasktype: tasktype
		}
	},
	methods: {
		cancleSignHandle() {
			this.$emit('cancleSignHandle', this.dataSource)
		},
		signHandle() {
			this.$emit('signHandle', this.dataSource)
		},
		detailHandle() {
			this.$emit('detailHandle', this.dataSource)
		},
		workHandle() {
			this.$emit('workHandle', this.dataSource)
		}
	}
}
